import { sessionHelper } from "./helpers/session"

function errorHandler(response) {
  return response.json().then(function(json) {
    if(response.ok) {
      return json;
    }
    if(response.status===401) {
      return sessionHelper.logout();
    }
    throw new Error(json.errors);
  });
}

export const nanoAPI = {
  signup(data) {
    return fetch('/api/register', {
      method: 'POST',
      body: data,
    }).then(errorHandler);
  },

  login(data) {
    return fetch('/api/login', {
      method: 'POST',
      body: data,
    }).then(errorHandler);
  },

  logout() {
    return fetch('/api/logout', {
      method: 'DELETE'
    }).then(errorHandler);
  },

  userProfile(username) {
    return fetch('/api/users/'+username, {
      method: 'GET'
    }).then(errorHandler);
  },

  timeline() {
    return fetch('/api/timeline', {
      method: 'GET'
    }).then(errorHandler);
  },

  userTweets(username) {
    return fetch('/api/users/'+username+'/tweets', {
      method: 'GET'
    }).then(errorHandler);
  },

  postTweets(data) {
    return fetch('/api/tweets', {
      method: 'POST',
      body: data,
    }).then(errorHandler);
  },

  followings() {
    return fetch('/api/follows/followings', {
      method: 'GET'
    }).then(errorHandler);
  },

  follow(id) {
    const data = new FormData();
    data.append('to_user_id', id);
    return fetch('/api/follows', {
      method: 'POST',
      body: data,
    }).then(errorHandler);
  },

  unfollow(id) {
    const data = new FormData();
    data.append('to_user_id', id);
    return fetch('/api/follows', {
      method: 'DELETE',
      body: data,
    }).then(errorHandler);
  },

  search(keyword) {
    return fetch('/api/search/tweets?keyword='+keyword+'&maxresults=100', {
      method: 'GET'
    }).then(errorHandler);
  },
};
